@use 'base/mixins' as m;
@use 'base/variables' as v;

.formulario {
    margin-top: 5rem;
    .campo {
        display: flex;
        align-items: center;
        margin-top: 2rem;

        label {
            flex: 0 0 10rem;
        }
        input {
            flex: 1;
            padding: 1rem;
            border: none;
            background-color: lighten(v.$gris, 40%);
        }
    }
    .btn {
        @include m.boton(v.$darkturquesa, v.$blanco);
    }

}