@use 'base/mixins' as m;
@use 'base/variables' as v;

.contenedor-nueva-tarea {
    display: flex;
    justify-content: center;

    .agregar-tarea {
        @include m.boton(v.$darkturquesa, v.$blanco);
    }
    // .eliminar-proyecto{
    //     @include m.boton(v.$tomato, v.$blanco);
    // }
}



.listado-tareas {
    list-style: none;
    padding: 0;
    margin-top: 3rem;

    .no-tareas {
        text-align: center;
    }
}

.tarea {
   padding: .5rem 2rem;
   border-bottom: 2px solid darken(v.$grisClaro, 5%);
   margin-bottom: 1rem;
   display: flex;
   justify-content: space-between;
    align-items: center;
   &:last-of-type {
    border-bottom: none;
   }

   p {
     font-size: 1.6rem;
   }

   .opciones {
       display: flex;
       gap: 1rem;
        button {
            border: none;
            font-size: 1rem;
            padding: .5rem 1rem;
            text-transform: uppercase;
            font-weight: 700;
            border-radius: 1rem;

            &:hover {
                cursor: pointer;
            }
        }
        .completa {
            background-color: v.$springGreen;
            color: v.$blanco;
        }
        .pendiente {
            background-color: v.$gold;
            color: v.$blanco;
        }
        .eliminar-tarea {
            background-color: v.$tomato;
            color: v.$blanco;
        }
   }
}